/**Dhanushi **/

/* Styling the body */
body {
  margin: 0;
  padding: 0;
}

/* Styling section, giving background
  image and dimensions */
section {
  width: 100%;
  height: 100vh;
}
.eventList {
  width: 100%;
}

.events {
  width: 100%;
}
/* Styling the left floating section */
section .leftBox {
  width: 50%;
  height: 100%;
  background-image: url(data:image/jpeg;base64,);
  background-repeat: no-repeat;
  float: left;

  padding: 50px;
  box-sizing: border-box;
  background-size: cover;
}

/* Styling the background of
  left floating section */
section .leftBox .content {
  height: 100%;
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
  padding: 40px;
  transition: 0.5s;
}

/* Styling the hover effect 
  of left floating section */
section .leftBox .content:hover {
  background: #e91e63;
}

/* Styling the header of left
  floating section */
section .leftBox .content h1 {
  margin: 0;
  padding: 0;
  font-size: 50px;
  text-transform: uppercase;
}

/* Styling the paragraph of
  left floating section */
section .leftBox .content p {
  margin: 10px 0 0;
  padding: 0;
}

/* Styling the three events section */
section .events {
  position: relative;
  width: 50%;
  height: 100%;
  transition: 0.5s;
  background: rgba(165, 44, 44, 0.5);
  float: right;
  box-sizing: border-box;
}

/* Styling the links of
the events section */
section .events ul {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);

  padding: 40px;
  box-sizing: border-box;
}

/* Styling the lists of the event section */
section .events ul li {
  list-style: none;
  background: #fff;
  box-sizing: border-box;
  height: 200px;
  margin: 15px 0;
}

/* Styling the time class of events section */
section .events ul li .time {
  position: relative;
  padding: 20px;
  background: #262626;
  box-sizing: border-box;
  width: 30%;
  height: 100%;
  float: left;
  text-align: center;
  transition: 0.5s;
}

/* Styling the hover effect
  of events section */
section .events ul li:hover .time {
  background: #e91e63;
}

/* Styling the header of time
  class of events section */
section .events ul li .time h2 {
  position: absolute;
  margin: 0;
  padding: 0;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff;
  font-size: 60px;
  line-height: 30px;
}

/* Styling the texts of time
class of events section */
section .events ul li .time h2 span {
  font-size: 30px;
}

.thankyou {
  background-attachment: fixed;
  background-repeat: no-repeat;
  height: 800px;
  width: 100%;
  background-position: center;
  background-image: url(/MERN_Project/static/media/ThankYou.8f3d7a40.jpg);
}
/* Styling the details
class of events section */
section .events ul li .details {
  padding: 20px;
  background: #fff;
  box-sizing: border-box;
  width: 70%;
  height: 100%;
  float: left;
}

/* Styling the header of the
details class of events section */
section .events ul li .details h3 {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 22px;
}

/* Styling the lists of details
class of events section */
section .events ul li .details p {
  position: relative;
  margin: 10px 0 0;
  padding: 0;
  font-size: 16px;
}

/* Styling the links of details
class of events section */
section .events ul li .details a {
  display: inline-block;
  text-decoration: none;
  padding: 10px 15px;
  border: 1.5px solid #262626;
  margin-top: 8px;
  font-size: 18px;
  transition: 0.5s;
}

/* Styling the details class's hover effect */
section .events ul li .details a:hover {
  background: #e91e63;
  color: #fff;
  border-color: #e91e63;
}

/* Home Page CSS */
